import React from 'react';

let marked = require("marked");

export default class App extends React.Component{ 
  constructor(props){
    super(props)
    this.state = {
      markdown: " # Hello!\n\n## How are you?\n\n**I'm under** \n\n `<div> the water </div>`\n\n```\n// please help me\n\nfunction helpMe(me, water) {\n   for(let i=0; i < water.length; i++) {\n      if(water[i] === me) {\n         return water.splice(i, 1); \n      }\n   } \n }\n```\n> Here\n\n1. too\n2.much\n3.raining \n\n [ëêęëēėëêęë](https://www.youtube.com/watch?v=z6-FWJteNLI) \n\n ![Fishes](https://www.pngkit.com/png/full/2-28671_cods-group-of-fish-png.png)"
    };
  }
  updateMarkdown(markdown) {
    this.setState({ markdown });
  }
render(){
  return (
    <div className="App">
     <div className="container">
          <h1 id="header">Markdown Previewer</h1>
        <div id="editor-container" className="small-container">
          <h2 class="my-h2">Editor</h2>
          <textarea id="editor" 
          value={this.state.markdown}
          onChange={(e) => {
            this.updateMarkdown(e.target.value);
          }}></textarea>
        </div>
        <div id="preview-container" className="small-container">
          <h2 class="my-h2">Preview</h2>
          <div id="preview"
          dangerouslySetInnerHTML={{ __html: marked(this.state.markdown) }}
          ></div>
        </div>
      </div>
      <div id="bubbles">
        <div className="bubble" />
        <div className="bubble" />
        <div className="bubble" />
        <div className="bubble" />
        <div className="bubble" />
        <div className="bubble" />
      </div>
    </div>
  );}
}
